@import '../../../styles//variables.scss';

.popup{
    position: absolute;
    top: 50%;
    left:  calc(50% - 375px); 
    z-index: 1001;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 40px;
    max-width: 750px;
    width: 100%;
    background: $lite-ground;
    box-shadow: $box-shadow;
    border-radius: $border-radius;
    background: $lite-ground;
}



.blur{
    /* position: absolute;
    z-index: 1000;
    width: 100vh;;
    height: 100vh;
    background-color: rgba(0,0,0, 0.25); */
}

.close{
    position: absolute;
    top: 20px;
    right: 20px;
    cursor: pointer;
    svg{
        transition: .3s ;
    }

    &:hover{
        svg{
            transform: rotate(360deg);
        }
    }
}


.day{
    .day__temp{
        font-weight: 500;
        font-size: 60px;
        color: $primary-color;
        margin-bottom: 12px;
    }

    .day__name{
        font-size: 25px;
        line-height: 30px;
        color: #000;
        margin-bottom: 12px;
    }

    .img{
        width: 53px;
        height: 53px;
        margin-bottom: 12px;
    }

    .day__time,
    .day__city{
        font-size: 15px;
        color: $base-color;
    }
}

.hide{
    display: none;
}